import initialState from './initial-state';

import {CALL_ERROR} from '../base/actions'
import {
    FLIP_STATE, SELECT_PANEL, SHOW_ALERT, HIDE_ALERT, TOGGLE_ALERT, LOADING_ACTION
    , ADVANCE_PROGRESS, CAROUSEL_CHANGED, INPUT_UPDATED
} from './actions'

export default function Experiment(state = initialState, action) {
    switch (action.type) {

        case INPUT_UPDATED:
            return state.set(action.property, action.payload);

        case CAROUSEL_CHANGED:
            return state.merge(state, {
                index: action.payload.index,
                direction: action.payload.dir
            });

        case ADVANCE_PROGRESS:
            return state.set('progress', action.payload);

        case TOGGLE_ALERT:
            return state.set('alertVisible', !state.get('alertVisible'));

        case HIDE_ALERT:
            return state.set('alertVisible', false);

        case SHOW_ALERT:
            return state.set('alertVisible', true);

        case SELECT_PANEL:
            return state.set('activeKey', action.payload);

        case LOADING_ACTION:
            return state.set('isLoading', action.payload);

        case FLIP_STATE:
            return state.merge(state, {
                open: !state.get('open')
            });

        case CALL_ERROR:
            return state.merge(state, {
                open: false,
                activeKey: 1,
                alertVisible: false,
                error: undefined
            });

        default:
            return state;
    }

}
