'use strict';
import React from 'react';
import {Panel, Button} from 'react-bootstrap'
import {connect} from "react-redux";
import {flip} from '../actions'

export default connect((state) => state.Experiment.toJSON())
((state) => {
        return <div>
            <h2>Panels</h2>
            <div>
                <Panel
                    onToggle={() =>{} }
                    expanded={state.open}>
                    <Panel.Heading>
                        <Panel.Title>Title that functions as a collapse toggle</Panel.Title>
                        <Button
                            onClick={() => state.dispatch(flip())}>
                            {!state.open ? "Open" : "Close"}
                        </Button>
                    </Panel.Heading>
                    <Panel.Collapse>
                        This is hidden until you click the button
                    </Panel.Collapse>
                </Panel>
            </div>
        </div>
    }
)