'use strict';
import React from 'react';
import {connect} from "react-redux";
import PropTypes from 'prop-types';
import ClassNames from 'classnames';

class Picture extends React.Component {
    render() {
        let classes = ClassNames({
            'img-responsive': this.props.responsive,
            'img-portrait' : this.props.portrait,
            'img-rounded': this.props.rounded,
            'img-circle': this.props.circle,
            'img-thumbnail': this.props.thumbnail
        });

        return (
            <picture>
                {
                    this.props.imgSet.map(
                        (img, idx) => {
                        return <source key={idx}
                                   media={img.media}
                                   srcSet={img.src}/>
                        }
                    )
                }
                {
                    <img className={classes}
                         src={this.props.defaultImage.src}
                         width={this.props.width}
                         height={this.props.height}
                         alt={this.props.defaultImage.alt}/>
                }
            </picture>
        );
    }
}

Picture.propTypes = {
    imgSet: PropTypes.arrayOf(
        PropTypes.shape({
            media: PropTypes.string.isRequired,
            src: PropTypes.string.isRequired
        }).isRequired
    ),
    defaultImage: PropTypes.shape({
        src: PropTypes.string.isRequired,
        alt: PropTypes.string.isRequired
    }).isRequired,
    rounded: PropTypes.bool,
    portrait: PropTypes.bool,
    circle: PropTypes.bool,
    thumbnail: PropTypes.bool,
    width: PropTypes.any,
    height: PropTypes.any
};
Picture.getDefaultProps = {
    imgSet: [],
    defaultImage: {},
    rounded: false,
    circle: false,
    thumbnail: false,
    portrait: false,
    width: "auto",
    height: "auto"
};

export default connect((state) => state.Experiment.toJSON())
(Picture)