'use strict';
import {Col, Grid, Row, Button} from 'react-bootstrap';
import {connect} from "react-redux";
import LogoutButton from '../login/logout-button.jsx'
import store from '../store'
import {USER_COMPANIES_REQUESTED, USER_COMPANY_SELECTED} from "../company/actions";
import React, {Component} from 'react';
import {push} from 'react-router-redux'

class Company extends Component {

    constructor(...args) {
        super(...args);

        store.dispatch({
            type: USER_COMPANIES_REQUESTED
        });
    }

    render() {
        console.log('STORE', store);
        const {companies, error} = this.props;
        return (
            <Grid>
                {companies.map((company) =>
                    <Col key={company.id}>
                        <Col>
                            <Row>
                                <Col xs={3}>Company Name:</Col>
                                <Col xs={3}>{company.name}</Col>
                            </Row>
                            <Row>
                                <Col xs={3}>Tax Number</Col>
                                <Col xs={2}>{company.taxNumber}</Col>
                            </Row>
                        </Col>
                        <Col>
                            <Button onClick={(e) => {
                                store.dispatch({
                                    type: USER_COMPANY_SELECTED,
                                    payload: company
                                });
                                store.dispatch(push('/invoices'))
                            }
                            }>Issue Invoices For This Company</Button>
                        </Col>
                    </Col>
                )}
                <Col xs={12}>
                    <LogoutButton/>
                </Col>
                <Col>
                    <div> {error} </div>
                </Col>
            </Grid>
        )
    }
}


function mapStateToProps(state) {
    return state.Company.toJSON();
}

export default connect(mapStateToProps)(Company)