'use strict';

export const FLIP_STATE = 'FLIP_STATE';
export const SELECT_PANEL = 'SELECT_PANEL';
export const SHOW_ALERT = 'SHOW_ALERT';
export const HIDE_ALERT = 'HIDE_ALERT';
export const TOGGLE_ALERT = 'TOGGLE_ALERT';
export const LOADING_ACTION = 'LOADING_ACTION';
export const TRIGGER_LOADING_ACTION = 'TRIGGER_LOADING_ACTION';
export const START_PROGRESS = 'START_PROGRESS';
export const ADVANCE_PROGRESS = 'ADVANCE_PROGRESS';
export const CAROUSEL_CHANGED = 'CAROUSEL_CHANGED';
export const INPUT_UPDATED = 'INPUT_UPDATED';

export function changeCarousel(index, dir) {
    return {
        type: CAROUSEL_CHANGED,
        payload: {
            index: index,
            dir: dir
        }
    }
}

export function inputUpdated(propertyName, value) {
    return {
        type: INPUT_UPDATED,
        property: propertyName,
        payload: value
    }
}

export function advanceProgress(progress) {
    return {
        type: ADVANCE_PROGRESS,
        payload: progress
    }
}

export function loading(flag) {
    return {
        type: LOADING_ACTION,
        payload: flag
    }
}

export function toggleAlert() {
    return {
        type: TOGGLE_ALERT
    }
}

export function hideAlert() {
    return {
        type: HIDE_ALERT
    }
}

export function showAlert() {
    return {
        type: SHOW_ALERT
    }
}

export function flip() {
    return {
        type: FLIP_STATE
    }
}

export function select(activeKey) {
    return {
        type: SELECT_PANEL,
        payload: activeKey
    }
}