import initialState from './initial-state';
import {
    LOGOUT_USER,
    LOGIN_USER,
    LOGIN_FAIL,
    EMAIL_CHANGE,
    PASSWORD_CHANGE,
    NAME_CHANGE,
    SURNAME_CHANGE,
    REGISTER_SUCCESS,
    REGISTER_FAILED
} from './actions';
import debugObj from '../debug_local.jsx';

export default function Login(state = initialState, action) {

    switch (action.type) {
        case LOGIN_USER:
            localStorage.setItem('access-token', action.response.accessToken);
            return state.merge(state, {
                userData: {
                    name: action.response.userId
                },
                loggedIn: true,
                error: undefined,
                timestamp: action.timestamp
            });
        case LOGIN_FAIL:
        case REGISTER_FAILED:
            return state.merge(state, {
                userData: undefined,
                loggedIn: false,
                error: action.response || "Invalid login",
                timestamp: action.timestamp
            });
        case NAME_CHANGE:
            return updateCredentials(state, {
                name: action.payload
            });

        case SURNAME_CHANGE:
            return updateCredentials(state, {
                surname: action.payload
            });

        case EMAIL_CHANGE:
            return updateCredentials(state, {
                email: action.payload
            });

        case PASSWORD_CHANGE:
            return updateCredentials(state, {
                password: action.payload
            });

        case LOGOUT_USER:
            localStorage.removeItem('access-token');
            return state.merge(state, {
                credentials: {
                    email:'',
                    password:''
                },
                userData: undefined,
                loggedIn: false,
                error: undefined,
                timestamp: action.timestamp
            });
        case REGISTER_SUCCESS:
        default:
            return state;
    }

    function updateCredentials( state, payload ) {
        let c = state.get('credentials');
        c = c.merge(c,payload);
        return state.set('credentials', c);
    }
}
