import {call, fork, put, takeEvery} from 'redux-saga/effects'
import {push} from 'react-router-redux'
import {USER_COMPANIES_REQUESTED, COMPANY_REQUEST_FAILED, USER_COMPANIES_SUCCESS,  USER_COMPANY_SELECTED} from './actions'
import {CALL_ERROR} from '../base/actions'
import Api from './api'


function* requestUserCompanies(action) {
    try {
        const data = yield call(Api.userCompanies);
        if (data.errorCode || data.error) {
            yield put(registerResponse(COMPANY_REQUEST_FAILED, data.errorMessage || data.error));
        } else {
            if(!!data && data.length === 1) {
                yield put(registerResponse(USER_COMPANY_SELECTED, data[0]));
                yield put(push('/reports'));
            } else {
                yield put(registerResponse(USER_COMPANIES_SUCCESS, data));
            }
        }
    } catch (e) {
        console.log("ERROR", e);
        yield put(registerResponse(CALL_ERROR, e.message ));
    }
}

function registerResponse(type, response) {
    return {
        type: type,
        payload: response,
        timestamp: Date.now()
    }
}

export function* watchUserCompanies() {
    yield takeEvery(USER_COMPANIES_REQUESTED, requestUserCompanies)
}

export default function* rootSaga() {
    yield fork(watchUserCompanies);
}