import {CALL_ERROR} from '../base/actions'

export default class LoginApi {

    static _options(body) {
        return {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            method: 'POST',
            body: JSON.stringify(body)
        }
    }

    static userReports(companyId) {
        const token = localStorage.getItem('access-token');
        if (token === undefined)
            return {
                type: CALL_ERROR,
                error: 'Please log in'
            };

        return fetch('http://localhost:8081/report/api/user', {
            headers: {
                'Accept': 'application/json',
                'X-Company-Id': companyId || 1,
                'Authorization': 'Bearer eyAidHlwIjogIkpXVCIsICJhbGciOiAiSG1hY1NIQTEifQ==.eyAic3ViIjoiMSIsICJleHAiOiIyMjkxLTA5LTI2VDA5OjI4OjA4LjEzMiJ9.6aR6po4Er0RR8V3BTYVXUnFEU6U= ' + token
            }
        })
            .then(response => response.json());
    }

}
