'use strict';

import 'babel-polyfill'

import React, {Component} from 'react'
import ReactDOM from 'react-dom'
import store, {history, browserHistory} from './store'
import MainView from './layout/main-view.jsx'
import Register from './layout/register-view.jsx'
import Login from './layout/login-view.jsx'
import Company from './layout/companies.jsx'
import Report from './layout/reports.jsx'
import {Provider} from 'react-redux'
import {Route, Router} from 'react-router-dom'
import DevTools from './devtools.jsx';
import ProgressBar from './experiments/components/progressbar.jsx'
import GridLayout from './experiments/components/grid-layout.jsx'
import Navbar from './experiments/components/navbar.jsx'
import Well from './experiments/components/wells.jsx'
import Panels from './experiments/components/panels.jsx'
import PanelGroup from './experiments/components/panel-group.jsx'
import Alert from './experiments/components/alerts.jsx'
import MediaPanel from './experiments/components/media.jsx'
import Buttons from './experiments/components/buttons.jsx'
import Carousel from './experiments/components/carousels.jsx'
import Images from './experiments/components/images.jsx'
import FormFields from './experiments/components/form-fields.jsx'
import Glyphicons from './experiments/components/glyphicon.jsx'


class App extends Component {
    render() {
        return (
            <Provider store={store}>

                <Router history={history}>
                    <div>
                        <Route path='/' component={Navbar}/>
                        <div style={{marginTop: 50}}>
                            <Route exact path='/' component={MainView}/>
                            <Route path='/register' component={Register}/>
                            <Route path='/login' component={Login}/>
                            <Route path='/companies' component={Company}/>
                            <Route path='/reports' component={Report}/>
                            <Route path='/example/progress' component={ProgressBar}/>
                            <Route path='/example/grid' component={GridLayout}/>
                            <Route path='/example/carousel' component={Carousel}/>
                            <Route path='/example/images' component={Images}/>
                            <Route path='/example/buttons' component={Buttons}/>
                            <Route path='/example/media'
                                   render={() => <MediaPanel src="//www.youtube.com/embed/x7cQ3mrcKaY"/>}/>
                            <Route path='/example/panels' component={Panels}/>
                            <Route path='/example/panels' component={PanelGroup}/>
                            <Route path='/example/panels' component={Alert}/>
                            <Route path='/example/wells' component={Well}/>
                            <Route path='/example/forms' component={FormFields}/>
                            <Route path='/example/glyphicons' component={Glyphicons}/>
                        </div>
                        {/*<Route onEnter={checkAuth}>*/}
                        {/*<Route path='/login' component={Login} />*/}
                        {/*<Route path='/register' component={Register} />*/}
                        {/*<Route path='/dashboard' component={Dashboard} />*/}
                        {/*</Route>*/}
                        {/*<Route path='*' component={NotFound} />*/}
                        {/*<MainView/>*/}

                        <DevTools store={store}/>
                    </div>
                </Router>
            </Provider>
        );
    }
}

ReactDOM.render(<App/>,
    document.getElementById('react')
);
