'use strict';

import React, {
    Component
} from 'react';
import {
    connect
} from 'react-redux';
import {
    Row,
    Col,
    Input,
    FormGroup,
    FormControl,
    ControlLabel
} from 'react-bootstrap';
import LoginButton from './login-button.jsx';
import {
    changeEmail, changePassword, LOGIN_REQUESTED
} from './actions';

import debugObj from '../debug_local.jsx';

class LoginPanel extends Component {

    constructor(...args) {
        super(...args);
        this.onPasswordChange = this.onPasswordChange.bind(this);
        this.onEmailChange = this.onEmailChange.bind(this);
        this.handleSelect = this.handleSelect.bind(this);
    }

    handleSelect() {
        const {
            dispatch, credentials
        } = this.props;
        dispatch({
            type: LOGIN_REQUESTED,
            userData: {
                email: credentials.email,
                password: credentials.password
            }
        });
    }

    renderWelcomeMessage() {
        const {
            userData, error
        } = this.props;
        let response;
        if (undefined !== error) {
            response = error;
        } else if (undefined !== userData && undefined !== userData.name) {
            response = `Welcome ${userData.name}`;
        } else {
            response = '';
        }
        return (<div> {response} </div>);
    }

    onPasswordChange(e) {
        const {value} = e.target;
        if (e.which === 13 && value) {
            this.handleSelect();
        } else {
            this.props.dispatch(changePassword(value));
        }
    }

    onEmailChange(e) {
        this.props.dispatch(changeEmail(e.target.value));
    }

    renderInput() {
        return (
            <form>
                <div>
                    <FormGroup>
                        <ControlLabel>UserName</ControlLabel>
                        <FormControl type="text"
                                     ref="username"
                                     onKeyUp={this.onEmailChange}
                                     placeholder="username"/>
                        <FormControl.Feedback/>
                    </FormGroup>
                </div>

                <div>
                    <FormGroup>
                        <ControlLabel>Password</ControlLabel>
                        <FormControl type="password"
                                     ref="password"
                                     onKeyUp={this.onPasswordChange}
                                     placeholder="password"/>
                        <FormControl.Feedback/>
                    </FormGroup>
                </div>

                <LoginButton handleSelect={this.handleSelect}/>

            </form>);
    }

    render() {
        const {loggedIn, error} = this.props;
        let panel;
        if (loggedIn) {
            panel = <Row>
                <Col xs={12}>
                    {this.renderWelcomeMessage()}
                </Col>
            </Row>;
        } else {
            panel = <Row>
                <Col xs={12}>
                    <h3> Please log in </h3>
                </Col>
                <Col xs={12}>
                    {this.renderInput()}
                </Col>

            </Row>;
        }
        return (<div>
            {panel}
            <Row>
                <div>{error}</div>
            </Row>
        </div>);
    }
}

function mapStateToProps(state) {
    return state.Login.toJSON();
}

export default connect(mapStateToProps)(LoginPanel)
