import {applyMiddleware, combineReducers, compose, createStore} from 'redux';
import DevTools from './devtools.jsx';
import createSagaMiddleware from 'redux-saga'
import {createLogger} from 'redux-logger'
import rootSaga from './sagas';
import {routerMiddleware, syncHistoryWithStore, routerReducer} from 'react-router-redux'
import createHistory from 'history/createBrowserHistory'
import initialState from './login/initial-state';
import {default as companyInitState} from './company/initial-state'
import {default as invoiceInitState} from './report/initial-state'
import {default as experimentInitState} from './experiments/initial-state'
import {default as layoutInitState} from './layout/initial-state'
import Login from './login/login-reducer';
import Company from './company/reducer';
import Report from './report/reducer'
import Experiment from './experiments/reducer'
import Layout from './layout/reducer'

const logger = createLogger({
    // Ignore `CHANGE_FORM` actions in the logger, since they fire after every keystroke
    predicate: (getState, action) => action.type !== 'CHANGE_FORM'
});

const sagaMiddleware = createSagaMiddleware();
export const history = createHistory();
const configureStore = compose(
    applyMiddleware(
        logger,
        sagaMiddleware,
        routerMiddleware(history)
    ),
    DevTools.instrument()
)(createStore);

const initState = Object.assign(Object.assign(Object.assign(Object.assign(Object.assign({}, initialState), companyInitState), invoiceInitState), experimentInitState), layoutInitState);

const store = configureStore(combineReducers({
    Login: Login,
    Company: Company,
    Report: Report,
    Experiment: Experiment,
    Layout: Layout,
    routing: routerReducer
}), initState);

syncHistoryWithStore(history, store);

sagaMiddleware.run(rootSaga);

export default store;

