'use strict'

import React, {Component} from 'react';
import ClassNames from 'classnames';
import {connect} from "react-redux";
import PropTypes from 'prop-types';

class MediaPanel extends Component {

    constructor(...args) {
        super(...args);
    }

    render() {
        let responsiveStyle = ClassNames({
            'embed-responsive': true,
            'embed-responsive-16by9': this.props.wideScreen,
            'embed-responsive-4by3': !this.props.wideScreen
        });
        let divStyle, ifStyle;
        divStyle = this.props.height ?
            {paddingBottom: this.props.height} : null;
        ifStyle = this.props.height ?
            {height: this.props.height, width: this.props.width} : null;

        if (this.props.src) {
            if (this.props.type === 'video') {
                return (
                    <div className={responsiveStyle}
                         style={divStyle}>
                        <div><h2>Media</h2></div>
                        <iframe style={ifStyle}
                                className="embed-responsive-item"
                                src={this.props.src}
                                allowFullScreen={this.props.allowFullScreen}>
                        </iframe>
                    </div>
                )
            } else {
                return (
                    <div className={responsiveStyle}
                         style={divStyle}>
                        <div><h2>Media</h2></div>
                        <embed frameBorder="0"
                               src={this.props.src}
                               style={ifStyle}
                               allowFullScreen={this.props.allowFullScreen}
                        />
                    </div>
                )
            }
        } else {
            return null;
        }
    }

}

MediaPanel.propTypes = {
    wideScreen: PropTypes.bool,
    allowFullScreen: PropTypes.bool,
    type: PropTypes.string,
    src: PropTypes.string.isRequired,
    width: PropTypes.number,
    height: PropTypes.number
};

MediaPanel.defaultProps = {
    src: "",
    type: "video",
    wideScreen: false,
    allowFullScreen: false,
    width: 0,
    height: 0
};

export default connect((state) => state.Experiment.toJSON())
(MediaPanel)