import initialState from './initial-state';
import {COMPANY_REQUEST_FAILED, USER_COMPANIES_SUCCESS, USER_COMPANY_SELECTED, USER_COMPANIES_REQUESTED} from './actions';
import {CALL_ERROR} from '../base/actions'

export default function Company(state = initialState, action) {

    switch (action.type) {
        case USER_COMPANIES_REQUESTED:
            return state.merge(state, {
                companies: [],
                error: undefined,
                timestamp: action.timestamp
            });
        case USER_COMPANIES_SUCCESS:
            return state.merge(state, {
                companies: action.payload,
                error: undefined,
                timestamp: action.timestamp
            });
        case USER_COMPANY_SELECTED:
            return state.merge(state, {
                companies: [],
                selectedCompany: action.payload.id,
                error: undefined,
                timestamp: action.timestamp
            });

        case COMPANY_REQUEST_FAILED:
        case CALL_ERROR:
            return state.merge(state, {
                companies: [],
                error: action.payload,
                timestamp: action.timestamp
            });

        default:
            return state;
    }

}
