import {call, fork, put, takeEvery} from 'redux-saga/effects'
import { push } from 'react-router-redux'
import {
    LOGIN_FAIL,
    LOGIN_USER,
    LOGIN_REQUESTED,
    REGISTER_REQUESTED,
    REGISTER_FAILED,
    REGISTER_SUCCESS
} from "./actions";
import LoginApi from './api'

function* login(action) {
    try {
        const data = yield call(LoginApi.login, action.userData);
        if (data.errorCode || data.error) {
            yield put(registerResponse(LOGIN_FAIL, data.errorMessage || data.error));
        } else {
            yield put(registerResponse(LOGIN_USER, data));
            yield put(push('/companies'))
        }
    } catch (e) {
        console.log("ERROR", e);
        yield put(registerResponse(LOGIN_FAIL,  (e.responseJSON || {errorMessage: e.stausText}).errorMessage));
    }
}


function* register(action) {
    try {
        const data = yield call(LoginApi.register, action.userData);
        if (data.errorCode || data.error) {
            yield put(registerResponse(REGISTER_FAILED, data.errorMessage || data.error));
        } else {
            yield put(registerResponse(REGISTER_SUCCESS, data));
        }
    } catch (e) {
        console.log("ERROR", e);
        yield put(registerResponse(REGISTER_FAILED, (e.responseJSON || {errorMessage: e.stausText}).errorMessage));
    }
}

function registerResponse(type, response) {
    return {
        type: type,
        response: response,
        timestamp: Date.now()
    }
}

export function* watchLogin() {
    yield takeEvery(LOGIN_REQUESTED, login)
}

export function* watchRegister() {
    yield takeEvery(REGISTER_REQUESTED, register)
}

export default function* rootSaga() {
    yield fork(watchLogin);
    yield fork(watchRegister);
}