'use strict'

import React from 'react';
import {ButtonGroup, ButtonToolbar, DropdownButton, MenuItem, SplitButton, Button} from 'react-bootstrap'
import {connect} from "react-redux";
import {TRIGGER_LOADING_ACTION} from '../actions'

export default connect((state) => state.Experiment.toJSON())
((state) =>
    <div>
        <h2>Buttons</h2>
        <h5>Simple buttons</h5>
        <ButtonToolbar>
            <Button> Default </Button>
            <Button bsStyle="primary"> Primary </Button>
            <Button bsStyle="success"> Success </Button>
            <Button bsStyle="info"> Info </Button>
            <Button bsStyle="warning"> Warning </Button>
            <Button bsStyle="danger"> Danger </Button>
            <Button bsStyle="link"> Link </Button>
        </ButtonToolbar>
        <h5>Full-width buttons</h5>
        <ButtonToolbar>
            <Button
                bsStyle="primary"
                bsSize="xsmall"
                block>
                Extra small block button (full-width)
            </Button>
            <Button
                bsStyle="info"
                bsSize="small"
                block>
                Small block button (full-width)
            </Button>
            <Button
                bsStyle="success"
                bsSize="large"
                block>
                Large block button (full-width)
            </Button>
        </ButtonToolbar>
        <h5>Active, non-active and disabled buttons</h5>
        <ButtonToolbar>
            <Button>Default button - Non-active</Button>
            <Button active>Default button - Active</Button>
            <Button disabled>Default button - Disabled</Button>
        </ButtonToolbar>
        <h5>Loading state</h5>
        <Button
            bsStyle="primary"
            disabled={state.isLoading}
            onClick={() => state.dispatch({type:TRIGGER_LOADING_ACTION})}>
            {state.isLoading ? 'Loading...' : 'Loaded'}
        </Button>
        <h5> Groups and Toolbar</h5>
        <ButtonToolbar>
            <ButtonGroup>
                <Button> 1 </Button>
                <Button> 2 </Button>
                <Button> 3 </Button>
            </ButtonGroup>
            <ButtonGroup>
                <Button> 4 </Button>
                <Button> 5 </Button>
            </ButtonGroup>
        </ButtonToolbar>
        <h5> Dropdown buttons </h5>
        <ButtonToolbar>
            <DropdownButton
                title="Dropdown"
                id = "bg-nested-dropdown">
                <MenuItem
                    bsStyle="link"
                    eventKey="1">
                    Dropdown link 1
                </MenuItem>
                <MenuItem
                    bsStyle="link"
                    eventKey="2">
                    Dropdown link 2
                </MenuItem>
            </DropdownButton>

            <DropdownButton noCaret
                title="Dropdown noCaret"
                id="bg-nested-dropdown-nocaret">
                <MenuItem
                    bsStyle="link"
                    eventKey="1">
                    Dropdown link 1
                </MenuItem>
                <MenuItem
                    bsStyle="link"
                    eventKey="2">
                    Dropdown link 2
                </MenuItem>
            </DropdownButton>

            <DropdownButton
                dropup
                title="Dropup"
                id="bg-nested-dropdup">
                <MenuItem
                    bsStyle="link"
                    eventKey="1">
                    Dropdown link 1
                </MenuItem>
                <MenuItem
                    bsStyle="link"
                    eventKey="2">
                    Dropdown link 2
                </MenuItem>
            </DropdownButton>
            <SplitButton
                bsStyle="success"
                title="Splitbutton down"
                id="successbutton">
                <MenuItem eventKey="1"> Action 1</MenuItem>
                <MenuItem eventKey="2"> Action 2</MenuItem>
            </SplitButton>
            <SplitButton
                dropup
                bsStyle="success"
                title="Splitbutton up"
                id="successbutton">
                <MenuItem eventKey="1"> Action 1</MenuItem>
                <MenuItem eventKey="2"> Action 2</MenuItem>
            </SplitButton>
        </ButtonToolbar>
    </div>
)