'use strict';

import React from 'react';
import {ControlLabel, FormControl, FormGroup, Glyphicon, InputGroup}
    from 'react-bootstrap';
import {connect} from "react-redux";

export default connect((state) => state.Experiment.toJSON())(
    (store) => (<div>
        <FormGroup controlId='euroGlyph'>
            <ControlLabel>Euro</ControlLabel>
            <InputGroup>
                <InputGroup.Addon><Glyphicon glyph='euro'/></InputGroup.Addon>
                <FormControl type="text"/>
                <InputGroup.Addon>.00</InputGroup.Addon>
            </InputGroup>
        </FormGroup>
        <FormGroup controlId='usdGlyph'>
            <ControlLabel>USD</ControlLabel>
            <InputGroup>
                <InputGroup.Addon><Glyphicon glyph='usd'/></InputGroup.Addon>
                <FormControl type="text"/>
                <InputGroup.Addon>.00</InputGroup.Addon>
            </InputGroup>
        </FormGroup>

        <FontAwesome icon="facebook" />
    </div>)
)

export function FontAwesome({icon}) {
    if (icon) {
        return (<i className={"fa fa-" + icon}/>)
    } else {
        return null;
    }
}