'use strict';

import React, {
    Component
} from 'react';
import {
    connect
} from 'react-redux';
import {
    Row,
    Col,
    Input,
    FormGroup,
    FormControl,
    ControlLabel,
    Button
} from 'react-bootstrap';
import LoginButton from './login-button.jsx';
import {
    changeEmail, changePassword, changeName, changeSurname, REGISTER_REQUESTED
} from './actions';

import debugObj from '../debug_local.jsx';

class RegisterPanel extends Component {

    constructor(...args) {
        super(...args);
        this.onKeyUp = this.onKeyUp.bind(this);
        this.onPasswordChange = this.onPasswordChange.bind(this);
        this.onEmailChange = this.onEmailChange.bind(this);
        this.handleSelect = this.handleSelect.bind(this);
        this.onNameChange = this.onNameChange.bind(this);
        this.onSurnameChange = this.onSurnameChange.bind(this);
    }

    handleSelect() {
        const {
            dispatch, credentials
        } = this.props;
        dispatch({
            type: REGISTER_REQUESTED,
            userData: {
                email: credentials.email,
                password: credentials.password,
                name: credentials.name,
                surname: credentials.surname
            }
        });
    }

    renderWelcomeMessage() {
        const {
            userData, error
        } = this.props;
        let response;
        if (undefined !== error) {
            response = error;
        } else if (undefined !== userData && undefined !== userData.name) {
            response = `Registered ${userData.name}`;
        } else {
            response = '';
        }
        return (<div> {response} </div>);
    }

    onNameChange(e) {
        this.props.dispatch(changeName(e.target.value));
    }

    onSurnameChange(e) {
        this.props.dispatch(changeSurname(e.target.value));
    }

    onPasswordChange(e) {
        this.props.dispatch(changePassword(e.target.value));
    }

    onEmailChange(e) {
        this.props.dispatch(changeEmail(e.target.value));
    }

    onKeyUp(e) {
        const {
            value
        } = e.target;

        if (e.which === 13 && value) {
            this.handleSelect();
        }
    }

    renderInput() {
        return (
            <form>
                <div>
                    <FormGroup>
                        <ControlLabel>Email</ControlLabel>
                        <FormControl type="text"
                                     ref="email"
                                     onKeyUp={this.onEmailChange}
                                     placeholder="email"/>
                        <FormControl.Feedback/>
                    </FormGroup>
                </div>

                <div>
                    <FormGroup>
                        <ControlLabel>Password</ControlLabel>
                        <FormControl type="password"
                                     ref="password"
                                     onKeyUp={this.onPasswordChange}
                                     placeholder="password"/>
                        <FormControl.Feedback/>
                    </FormGroup>
                </div>
                <div>
                    <FormGroup>
                        <ControlLabel>Name</ControlLabel>
                        <FormControl type="text"
                                     ref="name"
                                     onKeyUp={this.onNameChange}
                                     placeholder="Name"/>
                        <FormControl.Feedback/>
                    </FormGroup>
                </div>
                <div>
                    <FormGroup>
                        <ControlLabel>Surname</ControlLabel>
                        <FormControl type="text"
                                     ref="surname"
                                     onKeyUp={this.onSurnameChange}
                                     placeholder="Surname"/>
                        <FormControl.Feedback/>
                    </FormGroup>
                </div>

                <Button onClick={this.handleSelect}>Register</Button>

            </form>);
    }

    render() {
        const {loggedIn, error} = this.props;
        let panel;
        if (loggedIn) {
            panel = <Row>
                <Col xs={12}>
                    {this.renderWelcomeMessage()}
                </Col>
            </Row>;
        } else {
            panel = <Row>
                <Col xs={12}>
                    <h3> Please register: </h3>
                </Col>
                <Col xs={12}>
                    {this.renderInput()}
                </Col>

            </Row>;
        }
        return (<div>
            {panel}
            <Row>
                <div>{error}</div>
            </Row>
        </div>);
    }
}

function mapStateToProps(state) {
    return state.Login.toJSON();
}

export default connect(mapStateToProps)(RegisterPanel)
