import initialState from './initial-state';
import {INVOICES_REQUESTED_SUCCESS, USER_INVOICES_REQUESTED} from './actions';
import {CALL_ERROR} from '../base/actions'

export default function Report(state = initialState, action) {

    switch (action.type) {
        case USER_INVOICES_REQUESTED:
            return state.merge(state, {
                reports: [],
                error: undefined,
                timestamp: action.timestamp
            });

        case INVOICES_REQUESTED_SUCCESS:
            return state.merge(state, {
                reports: action.payload,
                error: undefined,
                timestamp: action.timestamp
            });


        case CALL_ERROR:
            return state.merge(state, {
                reports: [],
                error: action.payload,
                timestamp: action.timestamp
            });

        default:
            return state;
    }

}
