'use strict';
import React from 'react';
import {Panel, PanelGroup, Accordion} from 'react-bootstrap'
import {connect} from "react-redux";
import {select} from '../actions'

export default connect((state) => state.Experiment.toJSON())
((state) => {
        return <div>
            <h2>Panels</h2>
            <div>
                <PanelGroup
                    accordion
                    id="accordion-example"
                    activeKey={state.activeKey}
                    onSelect={ (activeKey)=> state.dispatch(select(activeKey)) }
                >

                    <Panel
                        eventKey="1"
                        bsStyle = "info"
                    >
                        <Panel.Heading>
                            <Panel.Title toggle>Panel 1 - Controlled PanelGroup</Panel.Title>
                        </Panel.Heading>
                        <Panel.Body collapsible>
                            Panel 1 content
                        </Panel.Body>
                    </Panel>

                    <Panel
                        eventKey="2"
                        bsStyle = "info"
                    >
                        <Panel.Heading>
                            <Panel.Title toggle>Panel 2 - Controlled PanelGroup</Panel.Title>
                        </Panel.Heading>
                        <Panel.Body collapsible>
                            Panel 2 content
                        </Panel.Body>
                    </Panel>

                </PanelGroup>

                <PanelGroup
                    accordion
                    defaultActiveKey="12"
                    id="accordion-uncontrolled-example"
                >

                    <Panel
                        eventKey="11"
                        bsStyle = "info"
                    >
                        <Panel.Heading>
                            <Panel.Title toggle>Uncontrolled Panel 1 - Controlled PanelGroup</Panel.Title>
                        </Panel.Heading>
                        <Panel.Body collapsible>
                            Uncontrolled Panel 1 content
                        </Panel.Body>
                    </Panel>

                    <Panel
                        eventKey="12"
                        bsStyle = "info"
                    >
                        <Panel.Heading>
                            <Panel.Title toggle>Uncontrolled Panel 2 - Controlled PanelGroup</Panel.Title>
                        </Panel.Heading>
                        <Panel.Body collapsible>
                            Uncontrolled Panel 2 content
                        </Panel.Body>
                    </Panel>

                </PanelGroup>

                <Accordion
                    defaultActiveKey="22"
                    id="accordion-uncontrolled-example"
                >

                    <Panel
                        eventKey="21"
                        bsStyle = "info"
                    >
                        <Panel.Heading>
                            <Panel.Title toggle>Accordion 1 - Controlled PanelGroup</Panel.Title>
                        </Panel.Heading>
                        <Panel.Body collapsible>
                            Accordion  1 content
                        </Panel.Body>
                    </Panel>

                    <Panel
                        eventKey="22"
                        bsStyle = "info"
                    >
                        <Panel.Heading>
                            <Panel.Title toggle>Accordion  2 - Controlled PanelGroup</Panel.Title>
                        </Panel.Heading>
                        <Panel.Body collapsible>
                            Accordion  2 content
                        </Panel.Body>
                    </Panel>

                </Accordion>
            </div>
        </div>
    }
)