import {fork} from 'redux-saga/effects'

import {default as loginRootSaga} from '../login/login-saga'
import {default as companyRootSaga} from '../company/saga'
import {default as reportRootSaga} from '../report/saga'
import {default as experimentsRootSaga} from '../experiments/saga'

export default function* rootSaga() {
    yield fork(loginRootSaga);
    yield fork(companyRootSaga);
    yield fork(reportRootSaga);
    yield fork(experimentsRootSaga)
}