'use strict';

import React from 'react'
import {ProgressBar} from 'react-bootstrap'
import store from '../../store'
import {connect} from "react-redux";
import {START_PROGRESS} from '../actions'

//store.dispatch({type:START_PROGRESS});

class ProgressBarExample extends React.Component {

    constructor(...args) {
        super(...args);
    }

    componentDidMount() {
        this.props.dispatch({type:START_PROGRESS});
    }

    render() {
        const { progress } = this.props;
        return (
            <div>
                <h2>Progress Bar</h2>
                <ProgressBar active
                             label={`${progress + 1}%`}
                             now={progress}/>
                <ProgressBar striped
                             bsStyle="success"
                             now={progress}/>
                <ProgressBar now={progress}
                             label={`${progress + 1}%`}/>

                <ProgressBar>
                    <ProgressBar
                        bsStyle = "warning"
                        now = { 20 }
                        key = { 1 }
                        label = "System Files" />
                    <ProgressBar
                        bsStyle="danger"
                        active
                        striped
                        now = { 40 }
                        key = { 3 }
                        label = "Crunching" />
                </ProgressBar>
            </div>
        )
    }

}

export default connect((state) => state.Experiment.toJSON())
(ProgressBarExample)