'use strict';
import {Col, Grid, Row, Button} from 'react-bootstrap';
import {connect} from "react-redux";
import LogoutButton from '../login/logout-button.jsx'
import store from '../store'
import {USER_INVOICES_REQUESTED, INVOICES_REQUESTED_SUCCESS} from "../report/actions";
import React, {Component} from 'react';
import {push} from 'react-router-redux'

class Report extends Component {

    constructor(...args) {
        super(...args);

        store.dispatch({
            type: USER_INVOICES_REQUESTED,
            payload: this.props.selectedCompany
        });
    }

    render() {
        console.log('STORE REPORTS', store);
        const {reports, error} = this.props;
        return (
            <Grid>
                {reports.map((i) =>
                    <Col key={i.id}>
                        <Col>
                            <Row>
                                <Col xs={3}>Invoice Number:</Col>
                                <Col xs={3}>{i.invoiceNumber}, {i.issuedOn}</Col>
                            </Row>
                        </Col>
                    </Col>
                )}
                <Col xs={12}>
                    <LogoutButton/>
                </Col>
                <Col>
                    <div> {error} </div>
                </Col>
            </Grid>
        )
    }
}


function mapStateToProps(state) {
    return Object.assign(state.Report.toJSON(), {selectedCompany: state.Company.toJSON().selectedCompany});
}

export default connect(mapStateToProps)(Report)