import {call, fork, put, takeEvery} from 'redux-saga/effects'
import {loading, advanceProgress, TRIGGER_LOADING_ACTION, START_PROGRESS} from '../experiments/actions'
import {delay} from 'redux-saga'

function* showLoaderForSomeTime() {
    yield put(loading(true));
    yield call(delay, 2000);
    yield put(loading(false));
}

function* showProgressBar() {
    for(let i = 0; i < 100; i++) {
        yield put(advanceProgress(i));
        yield call(delay, 100);
    }
   // yield put({type:START_PROGRESS});
   // looks nice when uncommented :)
}

export function* watchLoading() {
    yield takeEvery(TRIGGER_LOADING_ACTION, showLoaderForSomeTime)
}

export function* watchProgress() {
    yield takeEvery(START_PROGRESS, showProgressBar)
}

export default function* rootSaga() {
    yield fork(watchLoading);
    yield fork(watchProgress);
}