import {call, fork, put, takeEvery} from 'redux-saga/effects'
import {INVOICES_REQUESTED_SUCCESS, USER_INVOICES_REQUESTED} from './actions'
import {CALL_ERROR} from '../base/actions'
import Api from './api'


function* requestUserReports(action) {
    try {
        const data = yield call(Api.userReports, action.payload);
        if (data.errorCode || data.error) {
            yield put(registerResponse(CALL_ERROR, data.errorMessage || data.error));
        } else {
            yield put(registerResponse(INVOICES_REQUESTED_SUCCESS, data));
        }
    } catch (e) {
        console.log("ERROR", e);
        yield put(registerResponse(CALL_ERROR, e.message));
    }
}

function registerResponse(type, response) {
    return {
        type: type,
        payload: response,
        timestamp: Date.now()
    }
}

export function* watchUserReports() {
    yield takeEvery(USER_INVOICES_REQUESTED, requestUserReports)
}

export default function* rootSaga() {
    yield fork(watchUserReports);
}