'use strict';

import React from 'react';
import {FormGroup, FormControl, ControlLabel, HelpBlock, Button, Checkbox, Radio}
    from 'react-bootstrap';
import {connect} from "react-redux";
import {inputUpdated} from '../actions'

function validateEmail(email) {
    let length = email.length;
    if (email
        .match(/^[^\s@]+@[^\s@]+\.[^\s@]+$/)) return 'success';
    else if (length > 5) return 'warning';
    else if (length > 0) return 'error';
}

function validatePassword(pw) {
    if (pw.length < 5) return null;
    let containsNumber = pw.match(/[0-9]/);
    let hasCapitalLetter = pw.toLowerCase() !== pw;
    return containsNumber && hasCapitalLetter ? 'success' :
        'error';
}

function FieldGroup({id, label, validationState, help, ...props}) {
    return (
        <FormGroup controlId={id} validationState={validationState}>
            <ControlLabel>{label}</ControlLabel>
            <FormControl {...props} />
            {help && <HelpBlock>{help}</HelpBlock>}
        </FormGroup>
    );
}

function SelectField({id, label, options, ...props}) {
    return (
        <FormGroup controlId={id}>
            <ControlLabel>{label}</ControlLabel>
            <FormControl {...props} >
                {options && options.map((o, idx) => <option key={idx} value={o.value}>{o.text}</option>)}
            </FormControl>
        </FormGroup>
    );
}

export default connect((state) => state.Experiment.toJSON())
((state) =>
    <form>
        <h2>Form Fields</h2>
        <FieldGroup id='nameFieldId'
                    label='Name'
                    onChange={(e) => state.dispatch(inputUpdated('name', e.target.value))}
                    type='text'
                    placeholder='Enter your name'/>
        <FieldGroup
            id='basicForm'
            validationState={validateEmail(state.email)}
            label='Email Address'
            type='text'
            onChange={(e) => state.dispatch(inputUpdated('email', e.target.value))}
            bsStyle={validateEmail(state.email)}
            placeholder='Enter your email'/>

        <FieldGroup id='passwordGroupId'
                    label='Password'
                    type='password'
                    validationState={validatePassword(state.password)}
                    onChange={(e) => state.dispatch(inputUpdated('password', e.target.value))}/>
        <FormControl.Feedback/>
        <HelpBlock>Password must have at least one number, capital and lower case letter. And at least 6
            characters</HelpBlock>


        <Checkbox checked readOnly>
            Checkbox
        </Checkbox>
        <Radio checked readOnly>
            Radio
        </Radio>

        <FormGroup>
            <Checkbox inline>1</Checkbox> <Checkbox inline>2</Checkbox>{' '}
            <Checkbox inline>3</Checkbox>
        </FormGroup>
        <FormGroup>
            <Radio name="radioGroup" inline>
                1
            </Radio>{' '}
            <Radio name="radioGroup" inline>
                2
            </Radio>{' '}
            <Radio name="radioGroup" inline>
                3
            </Radio>
        </FormGroup>

        <FieldGroup id="fileInput"
                    lable="file"
                    help="Instructions"
                    type="file"/>


        <SelectField id='singleSelect'
                     label='Select'
                     options={[{value: "1", text: "First select"}, {value: "2", text: "Second select"}]}
                     componentClass='select'
                     onChange={(e) => state.dispatch(inputUpdated('singleSelect', e.target.value))}/>

        <SelectField id='multiSelect'
                     label='Multi Select'
                     options={[{value: "1", text: "First select"}, {value: "2", text: "Second select"}]}
                     componentClass='select'
                     multiple
                     onChange={(e) => {
                         state.dispatch(inputUpdated('multiSelect', e.target.value))}
                     }/>



        <FormGroup controlId="formControlsTextarea">
            <ControlLabel>Textarea</ControlLabel>
            <FormControl componentClass="textarea" placeholder="textarea" />
        </FormGroup>

        <FormGroup>
            <ControlLabel>Static text</ControlLabel>
            <FormControl.Static>email@example.com</FormControl.Static>
        </FormGroup>

        <Button type='submit'
                bsStyle='success'
                disabled={!(validatePassword(state.password) === validateEmail(state.email))}
        >Submit this form </Button>
    </form>
)