'use strict'

import React from 'react';
import {Alert, Button} from 'react-bootstrap'
import {connect} from "react-redux";
import {hideAlert} from '../actions'

export default connect((state) => state.Experiment.toJSON())
( (state) =>
    (state.alertVisible)?
        <Alert
            bsStyle="danger"
            onDismiss={ () => state.dispatch(hideAlert())}
        >
            <h4>An error has occurred!</h4>
            <p>Try something else</p>
            <Button
                onClick={() => state.dispatch(hideAlert())}
            > Forget It</Button>
        </Alert>
        : null
)